<template>
  <div>
    <h1>短信回复</h1>
    <a-divider />
    <div class="row a-row flex-center">
      <div id="add" class="col-md-18">
        <div class="flex-start">
          <a-table
            :columns="columns"
            :dataSource="groupList"
            :loading="loading"

            @change="handleTableChange"
          >
          </a-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
const axios = require('axios')
const service = axios.create({
  baseURL: process.env.API_HOST,
  timeout: 5000,
  withCredentials: true
})
const columns = [{
  title: 'Mobile',
  dataIndex: 'mobile',
  width: '20%'
}, {
  title: 'Text',
  dataIndex: 'text'
}, {
  title: 'Time',
  dataIndex: 'time',
  sorter: true,
  width: '20%'
}]
export default {
  name: 'smsReply',
  methods: {
    handleTableChange (pagination, filters, sorter) {
      console.log(sorter)
      if (sorter.order === 'descend') {
        console.log('gotyou')
        this.groupList = this.groupList.sort(this.cmp)
      } else {
        this.groupList = this.groupList.sort(this.cmp).reverse()
      }
    },
    cmp (a, b) {
      return a.id - b.id
    }
  },

  data () {
    return {
      groupList: [],
      loading: false,
      pagination: {},
      columns
    }
  },
  mounted: function () {
    service
      .get('/message/sms/reply', { timeout: 4000 })
      .then(function (response) {
        this.groupList = response.data.data.reverse()
        console.log(this.groupList)
        this.groupList = this.groupList.map(function (value) {
          var timestamp = value.time
          value.time = moment(timestamp * 1e3).format('YYYY-MM-DD HH:mm:ss')
          return value
        })
      }.bind(this))
      .catch(function (error) {
        if (error.response !== undefined) {
          this.$message.error(error.response.data.msg)
        } else {
          console.log(error.response)
        }
      }.bind(this)
      )
  }
}
</script>

<style>
</style>



